<template>
    <div class="oldIframDialog">
        <iframe :src="src" frameborder="0" :style="{height:fullHeight-5 +'px',width:fullWidth-5 +'px'}"></iframe>
    </div>
</template>
<script>
export default {
    name: 'oldIframDialog',
    data() {
        return {
            src: '',
            // 可视区域高
            fullHeight: document.documentElement.clientHeight,
            fullWidth: document.documentElement.clientWidth
        }
    },
    created() {
        // var oldHref = location.href.split('?')[1];
        // this.src = oldHref.substring(4);
        // this.src = localStorage.oldSrc + '&autoClose=0';
        this.src = this.$route.query.url + '&autoClose=0';
    },
    mounted () {
      var that = this;
      window.onresize = () => {
            return (() => {
                window.screenWidth = document.documentElement.clientWidth
                that.fullWidth = window.screenWidth;
                window.screenHeight = document.documentElement.clientHeight;
                that.fullHeight = window.screenHeight;
            })()
        };
      window.onmessage = function (e) {
        var data = e.data;
        // console.log(JSON.stringify(data) ,'data')
        // 点击确认按钮的key
        if (data.key === 'refreshNewSystem') {
          window.opener.postMessage({
              key: 'refreshNewSystem'
          },'*')
        }
        // 通过externalClose 返回值来判断是否由主动关闭
        if(data.externalClose){
            //通过 delay 返回值判断是否延时关闭
            if(data.delay && data.delay > 0){
                setTimeout(() => {
                  var win = window.open('','_top');
                  win.close();
                }, data.delay);
            }else{
                var win = window.open('','_top');
                win.close();
            }
        }
      };
    }
}
</script>
<style lang="scss" scoped>
    .oldIframDialog{
        background: #ffffff;
    }
</style>



